/*全局物体类型创建*/
/* eslint-disable no-undef */
/* eslint-disable no-unused-vars */
/* eslint-disable no-console */
import Device from '../obj/obj_device';
import Walls from "../obj/obj_walls";
import Box from "../obj/obj_box";
import Lines from "../obj/obj_lines";
import Park from "../obj/obj_park";
import InstancedEx from "../obj/obj_instanced";
import GroupEx from "../obj/obj_group";
const create = () => {};
// 前台队列集合
create.objTypes =  {
  'Walls':Walls,
  'Device': Device,//mesh基础物体类
  'Box':Box,
  'Lines':Lines,
  'InstancedEx':InstancedEx,
  'Park':Park,
  'Group':GroupEx
}

create.setType = (name,type)=>{
  create.objTypes[name] = type;
}

create.getObjTypes = (name)=>{
  return create.objTypes[name];
}
/**
 * 创建物体
 * @param param
 * @returns {Promise<*>}a
 */
create.create = (data) => {
  let  obj = new create.objTypes[data.type](data.param);
  // 挂载用户数据
  obj.userData = data.param;
  if (data.complete) {
    data.complete(obj);
    delete data.complete;
  }
  return  obj;
};
export default create
