/* eslint-disable no-undef */
/* eslint-disable no-dupe-class-members */
import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader";
import * as THREE from "three";
import {getProduct,getProductMaterial} from "../../service/product"
const loader = new GLTFLoader();
const textureLoader =  new THREE.TextureLoader()
export default class ProductManager{
    constructor(){
        // 存储所有的模型对象
        this.productModelLists = []; // 所有模型对象集合
        this.productMaterialsList = [];// 所有图片资源集合
        this.productModelObjs = {}; // 所有gltf模型集合
        this.materialsObjs = {}; // 图片资源集合
        this.parkObj = null;
    }

    /**
     * 加载所有模型
     */
    async loadModels(){
        const that = this;
        this.productModelLists = await getProduct();
        await Promise.all(that.productModelLists.map(async (element) => {
            let model =  await that.loadModel(element.url);
            that.productModelObjs[element.name] = model;
        }));

    }

    setParkObj(node){
        this.parkObj = node;
    }

    /**
     * 获取load节点
     * @param {*} name 
     * @returns 
     */
    getParkChildNams(name){
        return this.parkObj.getChildByName(name)
    }
    /**
     * 加载单个模型
     * @param {*} url
     * @returns
     */
    async loadModel(url){
        return new Promise((resolve)=>{
            loader.load(url,  (gltf)=> {
                // gltf.scene.traverse( function ( object ) {
                //     if ( object.isMesh ) object.castShadow = true;
                // } );
                resolve(gltf);
            })
        })
    }


    /**
     * 加载所有图片资源
     */
     async loadMaterials(){
        const that = this;
        this.productMaterialsList = await getProductMaterial();
        await Promise.all(that.productMaterialsList.map(async (element) => {
            const model =  await that.loadMaterial(element.url);
            that.materialsObjs[element.name] = model;
        }));

    }
    /**
     * 加载单个模型
     * @param {*} url
     * @returns
     */
    async loadMaterial(url){
        return new Promise((resolve)=>{
            const material = textureLoader.load( url);
            resolve(material);
        })
    }


    /**
     * 获取图片资源
     * @param {*} name
     * @returns
     */
    getMaterialByName(name){
        return this.materialsObjs[name];
    }

    /**
     * 获取单个模型
     * @param {*} name
     * @returns
     */
    getModelByName(name){
        return this.productModelObjs[name];
    }

    /**
     * 获取ci信息
     * @param ci
     */
    getProduct(){

    }
}
