<template>
    <div
      :style="{ width: width + 'px', height: height + 'px' }"
      id="div3d"
      class="core"
    ></div>
</template>

<script>
/* eslint-disable no-undef */
export default {
  name: "ThreeLoad",
  props: {
    msg: String,
  },
  data() {
    return {
      width: null,
      height: null,
    };
  },
  methods: {
    init() {
      this.$nextTick(() => {
        META.init();
      });
    },
  },
  mounted() {
    this.width = this.$el.offsetWidth;
    this.height = this.$el.parentElement.offsetHeight;
    this.init();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.core {
  width: 100%;
  height: 100%;
  /* position: absolute; */
}

</style>
