/* eslint-disable no-undef */
import * as THREE from "three";
import Lines from "../obj/obj_lines";
import { Tween } from "@tweenjs/tween.js";
class FLy {
    constructor(app) {
        this.app = app;
        this.controls = app.render.controls;
        this.camera = app.camera;
        this.current = {
            x: this.camera.position.x,
            y: this.camera.position.y,
            z: this.camera.position.z,
            x1: this.controls.target.x,
            y1: this.controls.target.y,
            z1: this.controls.target.z
        };

        this.customizedObj = {
            // -439.0443372110081, y: 391.48642692640556, z: 4681.731786783385
            '对象002': {
                x: -439, //增加一些偏移量，使相机与目标物体之间有些距离，以便于观察
                y: 451,
                z: 4881,
                x1: 0,
                y1: 0,
                z1: 0
            },
            // {x: -3641.547739855345, y: 2199.633399526, z: -77.62106371215005}
            // 6721.414486729492, y: 3473.8716301081236, z: 3446.3325289262807
            'default': {
                x: -3641, //增加一些偏移量，使相机与目标物体之间有些距离，以便于观察
                y: 2199,
                z: -77,
                x1: 0,
                y1: 0,
                z1: 0
            },
            'tunnel': {
                // -111.74251405343863, y: 176.66465888727515, z: -920.12404618053
                x: -114, //增加一些偏移量，使相机与目标物体之间有些距离，以便于观察
                y: 176,
                z: -920,
                // {x: -46, y: 120, z: -932}
                x1: -46,
                y1: 120,
                z1: -932
            }
        }

        // 隧道集合
        this.tunnelArray = ['对象011', '对象012', '对象013', '对象014', '对象015', '对象016',
            '对象017', '对象018', '对象019', '对象020', '对象021', '对象022', '对象023',
            '对象024', '对象025', '对象026'];

    }

    getCustomizedPos(name) {
        // todo 临时
        if (this.tunnelArray.includes(name)) {
            name = 'tunnel';
        }
        return this.customizedObj[name]
    }

    getResultData(mesh) {
        let target = {}
        if (typeof mesh === 'object') {
            const { x, y, z } = mesh.getCenter && mesh.getCenter();
            const arr = mesh instanceof Lines ? [30, 20, 30] : [300, 500, 300];
            target = this.getCustomizedPos(mesh.name) || {
                x: x + arr[0], //增加一些偏移量，使相机与目标物体之间有些距离，以便于观察
                y: y + arr[1],
                z: z + arr[2],
                x1: x,
                y1: y,
                z1: z
            }
        } else {
            target = this.getCustomizedPos(mesh)
        }
        return target;

    }
    flyTo(mesh, fun) {
        const target = this.getResultData(mesh)
        this.tween = new Tween(this.current);
        // 这一部分一定要加上，不然flyTo动作完成后，
        // 不论点击任何位置，controls都会将lootAt指向flyTo之前的位置
        // 将controls 的观察目标设置为选中物体
        this.tween.onComplete(() => {
            let temp = new THREE.Vector3(target.x1, target.y1, target.z1);
            if (fun) fun();
            this.controls.target = temp;
        });
        // 每次更新相机位置和朝向
        this.tween.onUpdate(() => {
            META.fly.update();
        });
        this.tween.to(target, 1500);
        this.tween.start();
    }

    update() {
        this.camera.position.set(this.current.x, this.current.y, this.current.z);
        this.camera.lookAt(this.current.x1, this.current.y1, this.current.z1);
    }
    // 在threejs的animate函数中添加

    animate(time) {
        this.tween && this.tween.update();
        this.mixer && this.mixer.update(time);
    }

    // 沿轨道飞行
    motion() {
        // 通过类CatmullRomCurve3创建一个3D样条曲线
        const box = new THREE.BoxGeometry(5, 5, 5);
        //材质对象
        const mesh = new THREE.Mesh(box, new THREE.MeshLambertMaterial({
            color: 0x0000ff
        }));
        this.app.scene.add(mesh);
        mesh.position.set(-10, -50, -50)
        // 通过类CatmullRomCurve3创建一个3D样条曲线
        const curve = new THREE.CatmullRomCurve3([
            new THREE.Vector3(-10, -50, -50),
            new THREE.Vector3(10, 0, 0),
            new THREE.Vector3(8, 50, 50),
            new THREE.Vector3(-5, 0, 100)
        ]);
        // 样条曲线均匀分割100分，返回51个顶点坐标
        const points = curve.getPoints(100);
        console.log('points', points);//控制台查看返回的顶点坐标
        const geometry = new THREE.BufferGeometry();
        // 把从曲线轨迹上获得的顶点坐标赋值给几何体
        geometry.vertices = points
        const material = new THREE.LineBasicMaterial({

            color: 0x4488ff
        });
        const line = new THREE.Line(geometry, material);
        this.app.scene.add(line)
        // 通过Threejs的帧动画相关API播放网格模型沿着曲线做动画运动
        // 声明一个数组用于存储时间序列
        let arr = []
        for (let i = 0; i < 101; i++) {

            arr.push(i)
        }
        // 生成一个时间序列
        var times = new Float32Array(arr);
        var posArr = []
        points.forEach(elem => {

            posArr.push(elem.x, elem.y, elem.z)
        });
        // 创建一个和时间序列相对应的位置坐标系列
        var values = new Float32Array(posArr);
        // 创建一个帧动画的关键帧数据，曲线上的位置序列对应一个时间序列
        var posTrack = new THREE.KeyframeTrack('.position', times, values);
        let duration = 101;
        let clip = new THREE.AnimationClip("default", duration, [posTrack]);
        this.mixer = new THREE.AnimationMixer(mesh);
        let AnimationAction = this.mixer.clipAction(clip);
        AnimationAction.timeScale = 20;
        AnimationAction.play();
    }

    changeLookAt(t) {
        // 当前点在线条上的位置
        var position = curve.getPointAt(t);
        mesh.position.copy(position);

        // 返回一个点t在曲线上位置向量的法线向量
        const tangent = curve.getTangentAt(t);
        // 位置向量和切线向量相加即为所需朝向的点向量
        const lookAtVec = tangent.add(position);
        mesh.lookAt(lookAtVec);
    }
}
export default FLy