import * as THREE from "three";
import {InstancedMesh} from "three";
export default class InstancedEx extends InstancedMesh {
    constructor(app) {
        // 目前是缺失的
        const alls = [
            {
                pos:[Math.random() * 40 - 20,Math.random() * 40 - 20, Math.random() * 40 - 20],
                rotations:[0,0,0],
                scales:[1,1,1],
            }
        ];
        const geometry = new THREE.BoxGeometry(1,1,1);
        const material = new THREE.MeshStandardMaterial({
            //map:materialTools.map, // 环境贴图
            color:0x2194ce,
            roughness:0.5,//材质的粗糙程度
            metalness:0,//金属相似度
        });
        super(geometry, material, alls.length);
        this.type ='instancedEx';
        this.makeInstanced();
        const pos = JSON.parse(app._OFFSET_)
        this.position.set(pos[0],pos[1]+10,pos[2]);
        this.type ='Instanced';
        // this.
    }

    /**
     * 初始化所有物体
     */
    makeInstanced(alls=[]){
        const self = this;
        const matrix = new THREE.Matrix4();
        alls.forEach((cur,index)=>{
            self.randomizeMatrix(cur.pos,cur.rotations,cur.scales);
            self.setMatrixAt( index, matrix );
        })
    }

    /**
     * 通过id获取instanced物体
     */
    getInstancedId(){

    }

    /**
     * 生成动态位置
     * @type {function(*): void}
     */
    randomizeMatrix = function (rotations=[0,0,0],scales=[1,1,1]) {
        const position = new THREE.Vector3();
        const rotation = new THREE.Euler();
        const quaternion = new THREE.Quaternion();
        const scale = new THREE.Vector3();
        return function ( matrix ) {
            // 位置
            position.x = Math.random() * 40 - 20;
            position.y = Math.random() * 40 - 20;
            position.z = Math.random() * 40 - 20;
            // 旋转
            rotation.x = rotations[0];
            rotation.y = rotations[1];
            rotation.z = rotations[2];
            quaternion.setFromEuler( rotation );
            // 缩放
            scale.x = scales[0];
            scale.y = scales[1];
            scale.z = scales[2];
            matrix.compose( position, quaternion, scale );

        };

    }()
}
