/* eslint-disable no-undef */
import * as THREE from 'three';
import { OutlinePass } from 'three/examples/jsm/postprocessing/OutlinePass';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';
import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass';
const boomParams = {
    exposure: 1.2,
    bloomStrength: 0.1,
    bloomThreshold: 0.6,
    bloomRadius: 0.6
};
export default class environmentEffect {
    constructor(app) {
        this.app = app;
        // 脉冲
        this.pass = new GlitchPass();
        // 描边
        this.outLinePass = new OutlinePass(new THREE.Vector2(app.dom.offsetWidth, app.dom.offsetHeight), app.scene, app.camera);
        // 泛光
        this.bloomPass = new UnrealBloomPass(new THREE.Vector2(app.dom.offsetWidth, app.dom.offsetHeight), 1.5, 0.4, 0.85);
        // 添加添加pick线
        this.initOutLine();
        // // 添加boom
        // this.initBoom();
        // // 添加脉冲
        // this.effectGlitchPass();
        // 添加场景光
        this.addPointLight();
        // 添加背景颜色
        this.addBackgroundImg(window.baseImage+'curtain.png');
        // this.addSkyBoxCubemap();
        // 添加场景地板
        // this.addPlaneGrid();
        // this.addFog();
    }

    addHemiLight() {

        const dirLight = new THREE.DirectionalLight(0xffffff, 1);
        dirLight.color.setHSL(0.1, 1, 0.95);
        dirLight.position.set(- 1, 1.75, 1);
        dirLight.position.multiplyScalar(30);
        this.app.scene.add(dirLight);

        dirLight.castShadow = true;

        // const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.6);
        // hemiLight.color.setHSL(0.6, 1, 0.6);
        // hemiLight.groundColor.setHSL(0.095, 1, 0.75);
        // hemiLight.position.set(3006.452849809113, 3466.627800031605, 38.7962564494514);
        // this.app.scene.add(hemiLight);

        // const hemiLightHelper = new THREE.HemisphereLightHelper(hemiLight, 20);
        // this.app.scene.add(hemiLightHelper);
    }

    // 设置地板
    setFloor(object) {
        Utils.forMaterial(object.material, (material) => {
            material.color.setStyle("#040912");
        })
    }

    /**
     * 正常光和地板
     * @param {*} url 
     * @returns 
     */

    addRrdinaryGrid() {
        const gt = new THREE.TextureLoader().load("/textures/hardwood2_bump.jpg");
        const gg = new THREE.PlaneGeometry(10000, 10000);
        const gm = new THREE.MeshPhongMaterial({ color: 0x808080, map: gt });

        const ground = new THREE.Mesh(gg, gm);
        ground.rotation.x = - Math.PI / 2;
        ground.material.map.repeat.set(8, 8);
        ground.material.map.wrapS = ground.material.map.wrapT = THREE.RepeatWrapping;
        ground.material.map.encoding = THREE.sRGBEncoding;
        ground.receiveShadow = true;

        this.app.scene.add(ground);
    }



    addPointLight() {
        const bulbGeometry = new THREE.SphereGeometry(0, 16, 8);
        const bulbLight = new THREE.PointLight(0xffffff, 2, 0, 2);

        const bulbMat = new THREE.MeshStandardMaterial({
            emissive: 0xffffee,
            emissiveIntensity: 1,
            color: 0x000000
        });
        bulbLight.add(new THREE.Mesh(bulbGeometry, bulbMat));
        bulbLight.position.set(3006.452849809113, 3466.627800031605, 38.7962564494514);
        bulbLight.castShadow = true;
        this.app.scene.add(bulbLight);

        // 环境光
        const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.8);
        this.app.scene.add(hemiLight);
    }

    /**
     * 添加雾
     */
    addFog() {
        this.app.scene.fog = new THREE.Fog(0xffffff, 100, 160000);
    }

    /**
     * 添加天空盒
     */
    addSkyBoxCubemap(path = '/effectBox/skyboxPolar') {
        const loader = new THREE.CubeTextureLoader();
        const texture = loader.load([
            path + '/Sky_Space_Right.jpg',
            path + '/Sky_Space_Left.jpg',
            path + '/Sky_Space_Up.jpg',
            path + '/Sky_Space_Down.jpg',
            path + '/Sky_Space_Back.jpg',
            path + '/Sky_Space_Front.jpg',
        ]);
        this.app.scene.background = texture;
    }

    /**
     * 环境地板
     */
    addPlaneGrid() {
        // 形状
        const geometry = new THREE.PlaneBufferGeometry(10000, 10000);
        // 材质
        const material = new THREE.MeshPhongMaterial();
        const mesh = new THREE.Mesh(geometry, material);
        mesh.name = 'grid';
        mesh.material.color.setStyle('#f8f8f8');
        mesh.rotation.x = - Math.PI / 2;
        mesh.receiveShadow = true;
        this.app.scene.add(mesh);
    }

    /**
     * 原型地板
     */
    addCircleGrid() {
        const geometry = new THREE.CircleGeometry(5000, 60);
        const material = new THREE.MeshBasicMaterial({ color: 0xffff00 });
        const circle = new THREE.Mesh(geometry, material);
        circle.rotation.x = - Math.PI / 2;
        circle.receiveShadow = true;
        this.app.scene.add(circle);
    }

    /**
     * 添加颜色
     * @param color
     */
    addBackground(color) {
        this.app.scene.background = new THREE.Color(color || '#0e1829');
    }

    /**
     * 添加背景图
     */
    addBackgroundImg(url){
        const loader1 = new THREE.TextureLoader();
        const bgTexture = loader1.load(url);
        this.app.scene.background = bgTexture;
    }


    /**
     * 添加流动线
     */
    initOutLine() {
        // 定制化特效
        this.outLinePass.edgeStrength = Number(10);
        this.outLinePass.edgeGlow = Number(0.5);
        this.outLinePass.edgeThickness = Number(2.5);
        this.outLinePass.pulsePeriod = Number(2.5);
        this.outLinePass.visibleEdgeColor.set('#ed8e00');
        this.app.composer.addPass(this.outLinePass);
        this.app.outLinePass = this.outLinePass;
    }


    /**
     * 整体泛光
     */
    initBoom() {
        this.bloomPass.threshold = boomParams.bloomThreshold;
        this.bloomPass.strength = boomParams.bloomStrength;
        this.bloomPass.radius = boomParams.bloomRadius;
        this.app.composer.addPass(this.bloomPass);
    }

    /**
     * 
     */
    effectGlitchPass() {
        this.pass.renderToScreen = true;
        this.app.composer.addPass(this.pass)
    }
}


