/* eslint-disable no-undef */
import * as THREE from "three";
import { CSS2DObject } from 'three/examples/jsm/renderers/CSS2DRenderer.js';
// modifyDocument("lableTitleWarning", "red", "报警信息: 温度过高");
export function modifyDocument(id, color, value) {
    var dom = document.getElementById(id);
    dom.style.color = color;
    dom.textContent = value;
}


/**
 * 销毁tops
 * @param {*} name 
 */
export function removeLableScene(name) {
    window.META.scene.remove(window.META.scene.getObjectByName(name));
}

export function createSpriteShape() {
    /*1、创建一个画布，记得设置画布的宽高，否则将使用默认宽高，有可能会导致图像显示变形*/
    let canvas = document.createElement("canvas");
    canvas.width = 120;
    canvas.height = 120;
    /*2、创建图形，这部分可以去看w3c canvas教程*/
    let ctx = canvas.getContext("2d");
    ctx.fillStyle = "#ff0000";
    ctx.arc(50, 50, 50, 0, 2 * Math.PI);
    ctx.fill();
    /*3、将canvas作为纹理，创建Sprite*/
    let texture = new THREE.Texture(canvas);
    texture.needsUpdate = true; //注意这句不能少
    let material = new THREE.SpriteMaterial({ map: texture });
    let mesh = new THREE.Sprite(material);
    /*4、放大图片，每个精灵有自己的大小，默认情况下都是很小的，如果你不放大，基本是看不到的*/
    mesh.scale.set(100, 100, 1);
    window.META.scene.add(mesh);
    //  return mesh;
}



/**
 * 详情tips
 * @param {*} x 
 * @param {*} y 
 * @param {*} z 
 */
export function details(x = 0, y = 0, z = 0) {
    const div = document.createElement('div');
    const html = `<div></div><div></div>`;
    div.append(html);
    div.innerHTML = 'GDP';
    div.style.padding = '4px 10px';
    div.style.color = '#fff';
    div.style.fontSize = '16px';
    div.style.position = 'absolute';
    div.style.backgroundColor = 'rgba(25,25,25,0.5)';
    div.style.borderRadius = '5px';
    // div元素包装成为css2模型对象CSS2DObject
    const label = new CSS2DObject(div);
    label.position.x = x;
    label.position.y = y + 1;
    label.position.z = z;

    div.style.pointerEvents = 'none';//避免HTML标签遮挡三维场景的⿏标事件
    // 设置HTML元素标签在three.js世界坐标中位置
    // label.position.set(x, y, z);
    // 默认不显示
    label.visible = true;
    label.name = name;
}
/**
 * 地磁提示
 * @param {*} x 
 * @param {*} y 
 * @param {*} z 
 */
export function fieldTips(x = 0, y = 0, z = 0, name, { title, content }) {
    const div = document.createElement('div');
    const { offsetWidth } = META.dom;
    const { isScreen } = META;
    const width = isScreen ? '120' : offsetWidth * 0.08;
    const bottom = width * 0.23 * 0.021;
    div.style.width = width + 'px';
    div.style.height = isScreen ? '84px' : width * 0.6 + 'px';
    div.style.padding = isScreen ? '3px 10px 25px 10px' : `${bottom}px ${bottom * 3}px ${bottom}px ${bottom * 10}px`;

    div.style.color = '#fff';
    div.style.background = `url(${window.baseImage}tip/orangeTip.png) no-repeat`;
    div.style.backgroundSize = `100% 100%`;
    div.style.fontSize = width * 0.23 * 0.45 + 'px';
    div.style.position = 'absolute';
    div.style.zIndex = 1;
    // div元素包装成为css2模型对象CSS2DObject
    const label = new CSS2DObject(div);
    label.position.x = x;
    label.position.y = y + 1;
    label.position.z = z;
    label.name = name;
    div.style.pointerEvents = 'none';//避免HTML标签遮挡三维场景的⿏标事件
    const html = `
        <div>${title}</div>
        <div style="margin-top: 10px;">${content}</div>
    `;
    div.innerHTML = html;
    // 设置HTML元素标签在three.js世界坐标中位置
    // label.position.set(x, y, z);
    // 默认不显示
    label.visible = true;
    return label;
}

