/**
 * 地磁车检
 * @param {*} x 
 * @param {*} y 
 * @param {*} z 
 */
export function geomagneticCity(x, y, z, name, { title, content }) {
    const div = document.createElement('div');
    const { offsetWidth } = META.dom;
    const { isScreen } = META;
    const width = offsetWidth * 0.15;
    const contentSize = width * 0.23 * 0.4;
    const right = width * 0.20;
    const top = width * 0.23 * 0.061;
    const bottom = width * 0.23 * 0.021;
    div.style.width = width * 0.74 + 'px';
    div.style.height = width * 0.23 + 'px';
    div.style.padding = `${top}px 0px ${bottom}px ${right}px`;

    div.style.color = '#fff';
    div.style.background = `url(${window.baseImage}tip/geomagnetism.png) no-repeat`;
    div.style.backgroundSize = `100% 100%`;
    div.style.fontSize = width * 0.23 * 0.2 + 'px';
    div.style.position = 'absolute';
    div.style.zIndex = 1;
    // div元素包装成为css2模型对象CSS2DObject
    const label = new CSS2DObject(div);
    label.name = name;
    label.position.x = x;
    label.position.y = y + 1;
    label.position.z = z;
    div.style.pointerEvents = 'none';//避免HTML标签遮挡三维场景的⿏标事件
    const html = `
        <div>${title}</div>
        <div><img style="width: ${contentSize}px;
        margin-top: ${!isScreen?'0px':'15px'};
        margin-right:${!isScreen?'0px':'18px'} ;
        float: left;" 
        src=${window.baseUrl}'/image/tip/alarmiIcon.png'> <span style="margin-top:${!isScreen?'0px':'31px'};
        float: left;font-size:${contentSize * 0.5}px">${content}</span></div>
    `;
    div.innerHTML = html;
    // 设置HTML元素标签在three.js世界坐标中位置
    // label.position.set(x, y, z);
    // 默认不显示
    label.visible = true;
    return label;
}

