<template>
  <div class="panel" :style="{top:top}">
    <img :src="imgBg" />
  </div>
</template>

<script>
export default {
  name: "PanelContent",
  data() {
    return {
      top:window.innerWidth > 1920? '21%' : '30%',
      imgBg: `${require("@/assets/image/rightTip.png")}`,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.panel {
  width: 11%;
  position: absolute;
  right: 2%;
  top: 21%;
}
.panel img {
  width: 100%;
  float: right;
  padding: 1% 2%;
}
</style>
