// BOX类
import { Mesh } from "three"
import * as THREE from "three";
export default class Box extends Mesh{
  constructor(param){
    const cubeMat = new THREE.MeshStandardMaterial( {
      side: THREE.FrontSide,
      // shadowSide: THREE.BackSide,
      color:Math.random() * 0xffffff,
    });
    const {WIDTH = 100,HEIGHT = 200,DEPAT = 100,_OFFSET_} = param;
    const boxGeometry = new THREE.BoxBufferGeometry( WIDTH, HEIGHT, DEPAT );
    super(boxGeometry,cubeMat);
    this.type ='Box';
    if(_OFFSET_){
      const pos = JSON.parse(_OFFSET_)
      this.position.set(pos[0],HEIGHT/2,pos[2]);
    }
    this.castShadow = true;
    this.receiveShadow = true;
  }
}
