/* eslint-disable no-undef */
/* eslint-disable no-useless-constructor */
import { Object3D } from "three";
// 元宇宙基础类
export default class Park extends Object3D  {
    constructor(app){
        super(app);
        this.type ='Park';
        // 动画集合
        this.actions = {};
        const {_PRODUCT_,_OFFSET_} = app;
        if(_PRODUCT_.length > 0){
            const node = META.productManager.getParkChildNams(_PRODUCT_) || META.productManager.getModelByName(_PRODUCT_).scene;
            this.animations = node.animations;
            this.lastActiveAction = null;//上一次的动画
            this.copy(node);
            node.remove();
            if(_OFFSET_){
                const pos = JSON.parse(_OFFSET_);
                this.position.set(pos[0],pos[1],pos[2]);
            }
        }
    }

    init(){

    }
}
