/**
 * 删除tops
 */
export function removeParkDefult() {
    const app = window.META;
    const parentObj  =  app.scene.getObjectByName('对象001')
    alarmList.forEach((cur, index) => {
        const name = cur.type + index + 'default';
        const obj = app.scene.getObjectByName(name);
        parentObj.remove(obj);
    })
}