/* eslint-disable */
import * as THREE from "three";
import Lines from "../obj/obj_lines";
import { tunnelDefault, parkDefult, removeTunnelDefault,removeParkDefult } from "../lib/viewpoint"
class EventInput {
    constructor(app) {
        this.app = app;
        this.mouse = new THREE.Vector2();
        this.raycaster = new THREE.Raycaster();
        this.scene = this.app.scene.getObjectByName('pyScene') || this.app.scene;
        this.selectedObject = [];
        this.rootObj = null;
        this.count = 0;//右键双击
        this.timer = null;
        this.flyEnd = true;
        this.init();
    }
    init() {
        // 左键单击
        this.app.dom.addEventListener('click', this.addOnClick);
        // 左键双击
        this.app.dom.addEventListener('dblclick', this.onLeftDbClick);
        // 右击
        this.app.dom.oncontextmenu = this.rightDbCLick
    }


    getRootObj() {
        return META.eventInput.rootObj;
    }
    /**
     * 
     * @param {*} event 
     */

    /**
     * 左键单击
     */
    addOnClick(event) {
        event.preventDefault();
        META.eventInput.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        META.eventInput.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        META.eventInput.raycaster.setFromCamera(META.eventInput.mouse, META.camera);
        const pickObj = META.eventInput.getPickObj(META.eventInput.raycaster.intersectObject(META.eventInput.scene, true))
        if (pickObj) {
            META.outLinePass.selectedObjects = [];// [pickObj];
            META.eventInput.rootObj = pickObj;
        } else {
            META.outLinePass.selectedObjects = [];
            META.eventInput.rootObj = null;
        }
    }

    /**
     * 左键双击
     * @param {} ev 
     */
    onLeftDbClick(event) {
        event.preventDefault();
        META.eventInput.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        META.eventInput.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        META.eventInput.raycaster.setFromCamera(META.eventInput.mouse, META.camera);
        const pickObj = META.eventInput.getPickObj(META.eventInput.raycaster.intersectObject(META.eventInput.scene, true))
        if (pickObj) {
            if (META.eventInput.flyEnd) {
                // 销毁默认 
                removeParkDefult();
                META.eventInput.flyEnd = false;
                META.fly.flyTo(pickObj, () => {
                    if (pickObj instanceof Lines) {
                        META.isFlyControls = true;
                    }
                    META.eventInput.flyEnd = true;
                    // 加汽车tps
                    tunnelDefault();
                });
            }
            // const  {x,y,z} = pickObj.position
            // META.camera.position.set(x,y,z)
        }
    }

    /**
     * 右键双击
     * @param {*} event 
     */
    rightDbCLick(event) {
        // event.preventDefault();
        // META.eventInput.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        // META.eventInput.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        // META.eventInput.raycaster.setFromCamera(META.eventInput.mouse, META.camera);
        // const pickObj = META.eventInput.getPickObj(META.eventInput.raycaster.intersectObject(META.eventInput.scene, true))
        // if (pickObj && pickObj.parent) {
        //     META.fly.flyTo(pickObj);
        // } else{
        // todo
        if (META.eventInput.flyEnd) {
            META.eventInput.count += 1; // 连续点击次数
            META.eventInput.timer = setTimeout(function () {
                clearTimeout(META.eventInput.timer);
                if (META.eventInput.count > 1) {
                    META.eventInput.count = 0;
                    META.eventInput.flyEnd = false;
                    // 删除车
                    removeTunnelDefault();
                    // 处理点击事件
                    META.fly.flyTo('default', () => {
                        META.isFlyControls = false;
                        META.eventInput.flyEnd = true;
                        parkDefult();
                    });
                }
                META.eventInput.count = 0;
            }, 200)
        }
    }

    /**
     * pick
     */
    onPointerMove(event) {
        META.eventInput.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        META.eventInput.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        META.eventInput.raycaster.setFromCamera(META.eventInput.mouse, META.camera);
        const pickObj = eventLists.getPickObj(META.eventInput.raycaster.intersectObject(META.scene, true));
        if (pickObj) {
            META.outLinePass.selectedObjects = pickObj;
        } else {
            META.outLinePass.selectedObjects = [];
        }
    }

    /**
     * 获取物体
     * @param {*} arr 
     * @returns 
     */
    getPickObj(arr = []) {
        let pick = null;
        arr.forEach(({ object }) => {
            if (object && !object.getPickEnabled()) {
                return;
            }
            pick = object;
            // else if(object && object.getPickEnabled()){
            //     pick = object;
            // }
        })
        return pick;
    }
}

export default EventInput;