/* eslint-disable no-undef */
import * as THREE from "three";
import  {getDistance,midpoint,get2DPointAngle} from  '../tools/utils'
import {Mesh} from "three";
// 元宇宙基础类
export default class Walls extends Mesh  {
    constructor(app){
        // 首先二维点
        // const cubeMat = new THREE.MeshStandardMaterial( {
        //     color: 0xffffff,
        //     roughness:0.5,
        //     metalness:0.5
        // });
        // const {start,end,height,depth,PARENTWIDTH,PARENTDEPAT}  = app;
        // const w = getDistance(start[0],end[0],start[1],end[1])
        // const angle = get2DPointAngle(start[0],end[0],start[1],end[1]);
        // const boxGeometry = new THREE.BoxBufferGeometry(  w+depth, height, depth );
        // super(boxGeometry,cubeMat);
        // this.userData = app;
        // this.userData.width = w;
        // const pos = midpoint(start, end);
        // console.error('数学题',pos,angle)
        // this.position.set( pos[0]-PARENTWIDTH/2, height/2, pos[1]+PARENTDEPAT/2);
        // if(angle!==0 && angle!==180){
        //     this.rotation.y = - (angle/180*Math.PI);
        // }
        // this.castShadow = true;

        // 测试墙
        const {start,end,height,depth,PARENTWIDTH,PARENTDEPAT}  = app;
        const w = getDistance(start[0],end[0],start[1],end[1])
        const angle = get2DPointAngle(start[0],end[0],start[1],end[1]);
        const shapeBox = new THREE.Shape();

        shapeBox.moveTo( -depth, -0 );
        shapeBox.lineTo( -depth, height );
        shapeBox.lineTo( w,height  );
        shapeBox.lineTo( w, -0 );
        shapeBox.lineTo( -depth, -0 );

        /**
         * 掏门洞
         * @type {{bevelSize: number, bevelSegments: number, depth, bevelEnabled: boolean, bevelThickness: number}}
         */
        for(let cur in app._CHILDREN_){
            const {_PRODUCT_,_LOCATION_} = app._CHILDREN_[cur];
            const obj = META.productManager.getModelByName(_PRODUCT_).scene;
            const {width,height} = obj && obj.getWorldAABB();
            const path =  Walls.setDoorPosit(Number(_LOCATION_),width,height);
            shapeBox.holes.push( path );
        }


        const extrudeSettings = {
            depth:depth,
            bevelEnabled: false,
            bevelThickness: 1,
            bevelSize: 0,
            bevelSegments: 1
        };


        const geometry = new THREE.ExtrudeGeometry( shapeBox, extrudeSettings );
        super(geometry,new THREE.MeshPhongMaterial( {
                    color: 0xffffff,
                    roughness:0.5,
                    side: THREE.DoubleSide,
                    transparent : true,
                    opacity : 0.5 ,
                    metalness:0.5
                })
        );
        // 改变轴心
        geometry.center();
        const pos = midpoint(start, end);
        if(angle!==0 && angle!==180){
            this.rotation.y = angle/180*Math.PI;
        }
        this.opacity = 0.5 ;
        this.position.set( pos[0]-PARENTWIDTH/2, height/2, pos[1]+PARENTDEPAT/2-4);
        this.type ='Wall';
    }

    /**
     * 给门淘一个洞
     * @param pos
     * @param width
     * @param height
     */
    static setDoorPosit(pos,width,height){
        const smileyMouthPath = new THREE.Path();
        const start =pos-width/2;
        const end =pos+width/2;
        smileyMouthPath.moveTo(start,0);
        smileyMouthPath.lineTo(start,height);
        smileyMouthPath.lineTo(end, height);
        smileyMouthPath.lineTo(end, 0);
        smileyMouthPath.lineTo(start,0);//第5点
        return smileyMouthPath;
    }
}
