/* eslint-disable no-undef */
import $ from "jquery"
import { post } from "./ajax";
/**
 * 获取所有ci数据
 * @returns
 */
export const getScCis = (id) => {
    if(META.isMock){
        return new Promise((resolve) => {
            $.getJSON(`/mocks/getCi_${id}.json`, (data) => {
                resolve(data);
            });
        })
    }
    return post('/query/ci',{id})
}
