import { Object3D } from "three";
import * as THREE from "three";
import { parseVector3 } from '../tools/utils';
import { Mesh } from "three";

/** 3D坐标 **/
const v3 = new THREE.Vector3();
/** 包围盒盒模式 **/
const box3 = new THREE.Box3();
Object3D.prototype.selfToWorld = function selfToWorld(localPos, ignoreScale = true) {
    localPos = parseVector3(localPos);
    this.node.updateMatrixWorld();

    // 是否忽略缩放系数
    if (ignoreScale) {
        const position = new THREE.Vector3();
        const scale = new THREE.Vector3();
        const quaternion = new THREE.Quaternion();
        this.node.matrixWorld.decompose(position, quaternion, scale);

        var matrixWorld = new THREE.Matrix4();
        matrixWorld.compose(position, quaternion, new THREE.Vector3(1, 1, 1));

        return localPos.applyMatrix4(matrixWorld).toArray();
    } else {
        return this.node.localToWorld(localPos).toArray();
    }
};


/**
 * 获取物体包围盒和中心点
 * @returns {{depth, center, width, height}}
 */
Object3D.prototype.getWorldAABB = function () {
    const aabb = this.getFromObjAabb();
    const size = aabb.getSize(v3);
    const { x, y, z } = size;
    return {
        width: x,
        height: y,
        depth: z,
    }
}

/**
 * 获取物体包围盒和中心点
 * @returns {{depth, center, width, height}}
 */
Object3D.prototype.getCenter = function () {
    const aabb = this.getFromObjAabb();
    const size = aabb.center(v3);
    return size;
}

Object3D.prototype.getFromObjAabb = function () {
    const aabb = box3.setFromObject(this);
    return aabb;
}


/**
 * 设置是否能pick
 * @param bool
 */
Object3D.prototype.setPickEnabled = function (bool) {
    this.pickEnabled = bool;
}

/**
 * 获取pick状态
 * @returns {boolean|*}
 */
Object3D.prototype.getPickEnabled = function () {
    return this.pickEnabled === undefined ? true : this.pickEnabled;
}

/**
 * 将世界坐标转相对坐标
 * @param {*} pos 
 * @returns 
 */
Object3D.prototype.convertWorldToLocalPosition = function (pos) {
    let vec;
    if (pos.isVector3) {
        vec = pos.clone();
    } else {
        vec = new THREE.Vector3(pos[0], pos[1], pos[2]);
    }

    const resultPos = this.worldToLocal(vec);
    return [resultPos.x, resultPos.y, resultPos.z];

};

/**
 * 将相对坐标转换成世界坐标
 * @param {*} pos 
 * @returns 
 */
Object3D.prototype.convertLocalToWorldPosition = function (pos) {
    // this._recursionUpdate(this);
    let vec;
    if (pos.isVector3) {
        vec = pos.clone();
    } else {
        vec = new THREE.Vector3(pos[0], pos[1], pos[2]);
    }
    vec = new THREE.Vector3(pos[0], pos[1], pos[2]);

    const resultPos = this.localToWorld(vec);
    return [resultPos.x, resultPos.y, resultPos.z];
};


/**
 * 设置相对坐标
 * @param {*} pos 
 * @returns 
 */
Object3D.prototype.setLocalPostion = function (pos) {
    // this._recursionUpdate(this);
    let { x, z } = this.parent.position;
    let vec = new THREE.Vector3(x + pos[0], pos[1], z + pos[2]);
    this.position.set(vec.x, vec.y, vec.z);
};

/**
 * 销毁对象
 */
Object3D.prototype.destroy = function () {
    const self = this;
    this.traverse(function (obj) {
        if (obj.type === 'Mesh') {
            obj.geometry.dispose();
            obj.material.dispose();
        }
        self.remove(obj);
    })
    // 删除场景对象scene的子对象group
    this.parent.remove(this);
};

/**
 * 销毁对象
 */
Object3D.prototype.setOpacity = function (op) {
    if (this.material) this.material.opacity = op
};


/**
 * 销毁对象
 */
Object3D.prototype.destroyChildByName = function (name) {
    const self = this;
    this.traverse(function (obj) {
        if (obj.name === name) {
            if (obj instanceof Mesh) {
                obj.geometry.dispose();
                obj.material.dispose();
            }
            self.remove(obj);
            // window.META.scene.remove(obj);
        }
    })
};