import {Mesh} from "three"
import * as THREE from "three";
// import CustomSinCurve from "../extend/customSinCurve_Ex";
// import META from "../index";
export default class Lines extends Mesh{
  constructor(param){
    const {_POINTS_,_RADIUS_} = param;
    let points = [];
    JSON.parse(_POINTS_).forEach((cur)=>{
      points.push( new THREE.Vector3( cur[0],cur[1],cur[2],) );
    })
    const curve = new THREE.CatmullRomCurve3(points);
    const tubeGeometry = new THREE.TubeGeometry(curve, 300, _RADIUS_ || 1, 20, false);
    const tubeMaterial = new THREE.MeshStandardMaterial( { 
      color: 0x00ff0f,
      side: THREE.DoubleSide,
      transparent:true,
      // opacity:0 
    });
    super(tubeGeometry, tubeMaterial);
    // this.castShadow = true;
    this.type ='Lines';

    // const path = new CustomSinCurve( 5 );
    // const geometry = new THREE.TubeGeometry( path, 20, 0.1, 25, false );
    // const material = new THREE.MeshBasicMaterial( { color: 0x00ff00 })
    // super(geometry,material);
    // const pos = JSON.parse(_OFFSET_)
    // this.position.set(pos[0],0,pos[2]);
    // this.castShadow = true;
    // this.type ='Lines';

  }
}
