

/**
 * 获取所有模型资源
 * @returns
 */
export const getProduct =()=>{
    return new Promise((resolve)=>{
        const arr = [
            {
                'id':'demo',
                'url':'/models/demo/demo0528.gltf',
                'name':'demo'
            }
        ]
        resolve(arr);
    })
}

