/* eslint-disable no-undef */
import {getScCis} from '../../service/ci';
import create from "./obj_create";
const factory =()=> {}
// 所有物体集合
factory.objs = {}
/**
 * 构建物体
 * @param classId 物体数据库id
 * @param name 物体物体名称
 * @returns {null}
 */
factory.createObject = (param)=> {
  const {_PARENTDBID_,_ID_,_TYPE_} = param;
  // if(param._OFFSET_ && PARENTWIDTH && PARENTDEPAT){
  //   let _OFFSET_ = JSON.parse(param._OFFSET_);
  //   _OFFSET_[0]-=PARENTWIDTH/2;
  //   _OFFSET_[2]-=PARENTDEPAT/2;
  //   param._OFFSET_ = JSON.stringify(_OFFSET_);
  // }
  const createObj =   create.create({
    type:_TYPE_,
    complete: ()=> {
    },
    param
  });
  if(_ID_){
    factory.objs[_ID_] =  createObj;
  }
  const parent = factory.objs[_PARENTDBID_];
  parent ? parent.add(createObj): META.scene.add(createObj);
  createObj.init && createObj.init();
}

/**
 * 获取3D物体
 * @param dbid
 * @returns {*|null}
 */
factory.getObjectByDbid = (dbid)=> {
  return factory.objs[dbid];
}

/**
 * objs 所有3D物体
 * @param objs
 */
factory.getCis = (scId,fun)=> {
    getScCis(scId).then( async (data) => {
      let loading = 100/data.length;
      if(loading > 0) {
        loading = parseInt(loading)
      }
      factory.recursiveObj(data,loading);
      if(fun) fun();
    })
}

/**
 * 初始化物体
 * @param {*} data
 */
factory.recursiveObj = (data,loading)=> {
  for (let cur in data) {
    let obj = data[cur];
    // 实例化每个类型的物体
    obj._TYPE_ && factory.createObject(obj);

    if (obj._TYPE_ || obj._CHILDREN_) {
      if(obj._CHILDREN_){
        factory.recursiveObj(obj._CHILDREN_,loading);
      }
    }
  }
}
export default factory
