<template>
  <div class="business" :style="{top:top}">
    <img :src="imgBg" />
  </div>
</template>

<script>
export default {
  name: "BusinessWrap",
  data() {
    return {
      top:window.innerWidth > 1920? '28%' : '36%',
      imgBg: `${require("@/assets/image/leftTop.png")}`,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.business {
  position: absolute;
  width: 11%;
  left: 2%;
}
.business img {
  width: 100%;
  float: right;
  padding: 1% 2%;
}
</style>
