/* eslint-disable no-undef */
/* eslint-disable no-useless-constructor */
import { Group } from "three";
// 元宇宙基础类
export default class GroupEx extends Group  {
    constructor(app){
        super(app);
        this.type ='GroupEx';
        const {_OFFSET_} = app;
        if(_OFFSET_){
            const pos = JSON.parse(_OFFSET_);
            this.position.set(pos[0],pos[1],pos[2]);
        }
    }
}
