/* eslint-disable no-undef */
/* eslint-disable no-useless-constructor */
import { Object3D } from "three";
import * as THREE from "three";
// 元宇宙基础类
export default class Device extends Object3D  {
    constructor(app){
        super(app);
        this.type ='Device';
        // 动画集合
        this.actions = {};
        const {_PRODUCT_,_OFFSET_,_STYLE_,_NAME_,_ISPICK_} = app;
        const node = META.productManager.getParkChildNams(_PRODUCT_);
        if(node){
            this.animations = node.animations;
            this.isAnimations() && this.creatAnimations(node.scene,node.animations)
            this.lastActiveAction = null;//上一次的动画
            if(_STYLE_){
              for(const cur in _STYLE_){
                node.material[cur] = _STYLE_[cur];
              }
            }
            // node.material.color.setStyle('#040912');
            this.copy(node.material);
            this.name = _NAME_;
            this.setPickEnabled(_ISPICK_===undefined?true:_ISPICK_);
            // node.clear();
            // node.geometry.dispose();
            // node.matrial.dispose();
            if(_OFFSET_){
                const pos = JSON.parse(_OFFSET_);
                this.position.set(pos[0],pos[1],pos[2]);
            }
        }
    }

    init(){

    }

    /**
     * 判断是否有动画
     * @returns {[]|*}
     */
    isAnimations(){
        return this.animations.length > 0;
    }

    /**
     * 初始化所有动画
     */
    creatAnimations(node,animations){
        const states = [ 'Idle', 'Walking', 'Running', 'Dance', 'Death', 'Sitting', 'Standing' ];
        const emotes = [ 'Jump', 'Yes', 'No', 'Wave', 'Punch', 'ThumbsUp' ];


        const mixer = new THREE.AnimationMixer( node );

        for ( let i = 0; i < animations.length; i ++ ) {

            const clip = animations[ i ];
            const action = mixer.clipAction( clip );
            this.actions[ clip.name ] = action;

            if ( emotes.indexOf( clip.name ) >= 0 || states.indexOf( clip.name ) >= 4 ) {

                action.clampWhenFinished = true;
                action.loop = THREE.LoopOnce;

            }

        }
        this.fadeToAction();
    }

    fadeToAction( name = 'Walking', duration = 0.2 ) {
        const self = this;
        if(!this.actions[ name ]) return;
        if ( this.lastActiveAction ) {
            this.lastActiveAction.fadeOut( duration );
            this.actions[ name ].reset()
                .setEffectiveTimeScale( 1 )
                .setEffectiveWeight( 1 )
                .fadeIn( duration )
                .play();
            this.lastActiveAction = this.actions[ name ];
        }else{
            setInterval(()=>{
                self.actions[ name ].play();
            },300)
        }

    }

}
