<template>
  <div class="home">
    <Header />
    <BusinessWrap />
    <Layer />
    <PanelContent />
    <THREE />
    <Loading v-if="onLoad" :loadingTime="time" />
  </div>
</template>

<script>
/* eslint-disable no-unused-vars */
import THREE from "@/components/THREE.vue";
import Loading from "@/components/Loading.vue";
import Header from "@/components/layout/Header.vue";
import BusinessWrap from "@/components/layout/BusinessWrap.vue";
import PanelContent from "@/components/layout/PanelContent.vue";
import Layer from "@/components/layout/Layer.vue";

export default {
  name: "HomeView",
  components: {
    THREE,
    Loading,
    Header,
    BusinessWrap,
    PanelContent,
    Layer,
  },
  data() {
    return {
      time: 0,
      interval: null,
      onLoad: true,
    };
  },
  methods: {
    load() {
      this.interval = setInterval(() => {
        if (window.META.onLoad) {
          this.time = 100;
          this.onLoad = false;
          clearInterval(this.interval);
        } else {
          if (this.time > 90) {
            this.time = 95
          } else {
            this.time += 5;
          }
        }
      }, 20);
    },
  },
  mounted() {
    this.load();
  },
};
</script>
<style scoped>
.home {
  width: 100%;
  height: 100%;
}
</style>
